import React from 'react';

const withHeader = (Component) => {   
    return function (props) {
        console.log(props.selectedindex);
        return (
            <div className="container">
                <div className="header">
                    <h1>People</h1>
                </div>
                <Component {...props}/>
            </div>
        )
    }
}

export default withHeader;